* {
	margin: 0;
  padding: 0;
}

span {
  color: #fb9c06;
}
hr {
	height: 4px;
	border: 0;
	background-color: #6e6d6b;
	
}

.hr {		
	height: 4px;
	border: 0;
	background-color: #fb9c06;
}

  /* header */

.header {
	display: flex;	
  height: 120px;
}
.logo {
	padding-top: 4px;
	margin-left: 150px;
	
}
.logo-text {
	padding: 25px 0 0 50px;
}
.nav {
  margin-left: 150px;
  padding-top: 80px;
}
a {
  font-size: 22px;
	text-decoration: none;
	padding-left: 70px;
	color: #9e9b9b;
}

/* slider */
.slider {
	background: url(../img/big-orange-bg.png);
	width: 100%;
	height: 400px;
}
.slider-text {
	padding-top: 140px;
	padding-left: 240px;
	font-size: 37px;
	color: #fb9c06;
}
.slider-text2 {
	padding-top: 60px;
	padding-left: 240px;
	font-size: 37px;
	color: #6e6d6b;
}

/* container */

.container {
	max-width: 1200px;
	margin: 0 auto;
	padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
	
/* content */

}
.content {
	font-size: 22px;
	width: 300px;
	height: 200px;
	display: inline-block;
	padding: 20px 10px;
	text-align: left;
	margin-bottom: 40px;
}
.button {
	text-align: right;
}
.btn {
	font-style: italic;
	font-size: 20px;	
	color: #fb9c06;
}

/* team description */

.title {
  text-align: center;
  color: #ff9c00;
  font-size: 40px;
  text-decoration: underline;
  margin-bottom: 30px;
}
.block-team {
  max-width: 30%;
  display: inline-block;
}

.team-name {
  text-align: center;
  font-size: 22px;
  color: #6e6d6b;
  margin-bottom: 30px;
}
.team-description {
  text-align: left;
  font-size: 15px;
  line-height: 23px;
  margin: 0 60px;
}
.team-img {
  text-align: center;
}